import React, {useState} from 'react';

import './services.css';
import logo from '../../mangelo-logo-dark.png'

import {motion, useMotionValue, useTransform} from 'framer-motion';

import bg from '../../media/bg_tmp.jpg';

function Services(){

    const [activeEService,setActiveEService] = useState(true);
    const [activeCustom,setActiveCustom] = useState(false);
    const [activeMicro,setActiveMicro] = useState(false);
    const [activeApp,setActiveApp] = useState(false);
    const [activeUi,setActiveUi] = useState(false);

    const setActiveService = (serviceName) => {
        if (serviceName==='e'){
            setActiveEService(true)
            setActiveCustom(false)
            setActiveMicro(false)
            setActiveApp(false)
            setActiveUi(false)
        }else if (serviceName==='c'){
            setActiveEService(false)
            setActiveCustom(true)
            setActiveMicro(false)
            setActiveApp(false)
            setActiveUi(false)
        }else if (serviceName==='m'){
            setActiveEService(false)
            setActiveCustom(false)
            setActiveMicro(true)
            setActiveApp(false)
            setActiveUi(false)
        }else if (serviceName==='a'){
            setActiveEService(false)
            setActiveCustom(false)
            setActiveMicro(false)
            setActiveApp(true)
            setActiveUi(false)
        }else if (serviceName==='u'){
            setActiveEService(false)
            setActiveCustom(false)
            setActiveMicro(false)
            setActiveApp(false)
            setActiveUi(true)
        }
        console.log(serviceName)
    }
    

    const eCommerceTitle = 'E-COMMERCE';
    const eCommerceSubtitle = 'E-commerce & e-shops';
    const eCommerceDescription = 'Lorem ipsum dolor sit admet';
    const eCommerceSkills = ['shopify.png'];

    const customTitle = 'CUSTOM WEB';
    const customSubtitle = 'Custom Web design & development';
    const customDescription = 'Lorem ipsum dolor sit admet';
    const customSkills = ['dj.png','react.png','php.png'];

    const microTitle = 'MICROSITES AND LANDING PAGES';
    const microSubtitle = 'Microsites and Landign Pages';
    const microDescription = 'Lorem ipsum dolor sit admet';

    const appTitle = 'APP DEV & DESIGN';
    const appSubtitle = 'Web/Mobile App development & Design';
    const appDescription = 'Lorem ipsum dolor sit admet';

    const uiTitle = 'UI/UX';
    const uiSubtitle = 'UI & UX design';
    const uiDescription = 'Lorem ipsum dolor sit admet';

    const item = {
        hidden: { scale: 0 },
        visible: {
          scale: 1
        }
      };
    
    const container = {
        visible: {
            transition: {
            delayChildren: 1.1,
            staggerChildren: 0.1
            }
        }
    };

    return(
        <div id='services' className="servicesContainer">
            <div className='serviceTitle'>
                <h1 
                style={{color:'#15433D'}}
                >SERVICES</h1>
            </div>
            <div className='serviceColumns'>
                <div className='descriptionColumn'>
                    <motion.div 
                        className='descriptionElement' 
                        onClick={()=>{setActiveService('e')}}
                        style={{opacity:activeEService?1:0.5}}
                        whileHover={{scale:activeEService?1:0.97}}
                        initial={{y:20,opacity:0}}
                        whileInView={{y:0,opacity:activeEService?1:0.5}}
                        viewport={{ once: true }}
                        transition={{y:{delay:0.3},opacity:{delay:0.3}}}
                        >
                        <div className='descriptionLogo'>
                            <img src={logo} className="logo" alt="logo" />
                        </div>
                        <div className='description'>
                            <h3>{eCommerceTitle}</h3>
                            <h6>{eCommerceSubtitle}</h6>
                            <p>{eCommerceDescription}</p>
                        </div>
                    </motion.div>

                    <motion.div className='descriptionElement' 
                        onClick={()=>{setActiveService('c')}}
                        style={{opacity:activeCustom?1:0.5}}
                        whileHover={{scale:activeCustom?1:0.97}}
                        initial={{y:20,opacity:0}}
                        whileInView={{y:0,opacity:activeCustom?1:0.5}}
                        viewport={{ once: true }}
                        transition={{y:{delay:0.3},opacity:{delay:0.3}}}
                        >
                        <div className='descriptionLogo'>
                            <img src={logo} className="logo" alt="logo" />
                        </div>
                        <div className='description'>
                            <h3>{customTitle}</h3>
                            <h6>{customSubtitle}</h6>
                            <p>{customDescription}</p>
                        </div>
                    </motion.div>

                    <motion.div className='descriptionElement' 
                        onClick={()=>{setActiveService('m')}}
                        style={{opacity:activeMicro?1:0.5}}
                        whileHover={{scale:activeMicro?1:0.97}}
                        initial={{y:20,opacity:0}}
                        whileInView={{y:0,opacity:activeMicro?1:0.5}}
                        viewport={{ once: true }}
                        transition={{y:{delay:0.3},opacity:{delay:0.3}}}
                        >
                        <div className='descriptionLogo'>
                            <img src={logo} className="logo" alt="logo" />
                        </div>
                        <div className='description'>
                            <h3>{microTitle}</h3>
                            <h6>{microSubtitle}</h6>
                            <p>{microDescription}</p>
                        </div>
                    </motion.div>

                    <motion.div className='descriptionElement' 
                        onClick={()=>{setActiveService('a')}}
                        style={{opacity:activeApp?1:0.5}}
                        whileHover={{scale:activeApp?1:0.97}}
                        initial={{y:20,opacity:0}}
                        whileInView={{y:0,opacity:activeApp?1:0.5}}
                        viewport={{ once: true }}
                        transition={{y:{delay:0.3},opacity:{delay:0.3}}}
                        >
                        <div className='descriptionLogo'>
                            <img src={logo} className="logo" alt="logo" />
                        </div>
                        <div className='description'>
                            <h3>{appTitle}</h3>
                            <h6>{appSubtitle}</h6>
                            <p>{appDescription}</p>
                        </div>
                    </motion.div>

                    <motion.div className='descriptionElement' 
                        onClick={()=>{setActiveService('u')}}
                        style={{opacity:activeUi?1:0.5}}
                        whileHover={{scale:activeUi?1:0.97}}
                        initial={{y:20,opacity:0}}
                        whileInView={{y:0,opacity:activeUi?1:0.5}}
                        viewport={{ once: true }}
                        transition={{y:{delay:0.3},opacity:{delay:0.3}}}
                        >
                        <div className='descriptionLogo'>
                            <img src={logo} className="logo" alt="logo" />
                        </div>
                        <div className='description'>
                            <h3>{uiTitle}</h3>
                            <h6>{uiSubtitle}</h6>
                            <p>{uiDescription}</p>
                        </div>
                    </motion.div>
                </div>
                <motion.div 
                    className='logoColumn'
                    initial={{opacity:0}}
                    whileInView={{opacity:1}}
                    viewport={{once:true}}
                    transition={{delay:1}}
                    >
                    <motion.div 
                        className='container' 
                        variants={container}
                        initial="hidden"
                        whileInView="visible"
                        viewport={{ once: true }}
                        >
                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>
                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>

                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>
                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>

                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>
                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>

                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>
                        <motion.img src={logo} className="skillLogo" alt="logo" variants={item}/>

                    </motion.div>
                </motion.div>
            </div>
        </div>
    )
}

export default Services